import React from 'react';
import '../../css/Home/ContactMe.css';

export default class ContactMe extends React.Component {

  state = {
    name:'',
    email:'',
    message:''
  }

  handleInputChange = (ev) => {
    console.log(ev)
  }

  onSubmitContact = () => {
    this.props.onSubmitContact()
  }

  render(){
    const { name, email ,message } = this.state
    return (
      <footer className="contactMe" id='contactMe' ref={this.props.refC}>
          <div className="contactMeMain">
            <p className="contactMeTitle">CONTACT ME</p>
            <form className="contactMeForm">
                <input className="contactMeInput" type="text" name="name" placeholder="Name" />
                <input className="contactMeInput" type="text" name="email" placeholder="E-mail" value= {email}/>
                <textarea name="message" className="contactMeTextarea" placeholder="Leave Message Here..." value={message}></textarea>
                <a className="contactMeBtn" onClick={this.onSubmitContact}>Submit</a>
            </form>
          </div>
          
          <div className="contactMeBack"></div>
      </footer>
    );
  }
}
